<template>
  <div class="case">

    <div class="banner">
      <img src="../../common/images/case/c01.jpg"/>
    </div>

    <div class="box-pro">
      <div class="des">
        <div class="title-des-box">
          <div class="title">铸造更高品质的金银<br>文化产品</div>
          <div class="des-cont">每一枚纪念币·都是一件艺术品<br>
            中国黄金品质·国家造币工艺</div>
        </div>
        <ul>
          <li @click="choose(1)" :class="{'active':product_type ==1 }">金银币/章</li><li @click="choose(2)" :class="{'active':product_type ==2 }">金银条/钞</li><li @click="choose(3)" :class="{'active':product_type ==3 }">奖牌/奖章/徽章</li><li @click="choose(4)" :class="{'active':product_type ==4 }">金银摆件</li>
        </ul>
      </div>
      <div class="width-common" style="padding-top: 480px">
        <ul class="pro-list">
          <li v-for="item in data_product" @click="goDetail(item.id)">
            <img :src="URL_BASE+item.img_url"/>
            <span class="pro-title">{{item.product_name}} >> </span>
            <div class="box-cover">
              <span class="cover-title">{{item.product_name}}</span>
              <span class="cover-cont">{{item.descrip}}</span>
            </div>
          </li>

        </ul>


      </div>

    </div>

    <div class="box-center">
      <img src="../../common/images/case/c08.jpg"/>
    </div>

    <CaseMore style="margin-top: 50px" :titleShow ="false"></CaseMore>



  </div>
</template>
<script>
  import {PopAlert, ShowLoading, HideLoading} from 'common/js/mylayer'
  import {URL_BASE} from 'common/js/public'
  import {MyPost,MyGet} from 'common/js/request'
  import CaseMore from  '@/components/case-more/case-more'
  export default {
    data(){
      return{
        product_type:1,
        data_product:'',
        URL_BASE:''
      }
    },
    components:{
      CaseMore
    },
    created(){
      if(this.$route.query.product_type){
        this.product_type = this.$route.query.product_type

      }

      this.getCaseSort()
      this.URL_BASE = URL_BASE;
    },
    mounted(){
      document.documentElement.scrollTop = document.body.scrollTop =0;
    },
    methods:{
      goDetail(id){
        this.$router.push({
          path:'/detail',
          query:{
            id:id
          }

        })
      },
      choose(product_type){
        this.product_type = product_type;
        this.getCaseSort();
      },
      getCaseSort() { // 初始请求
        MyPost(URL_BASE+'/index.php/api/product/getCategoryProductList',
          {
            product_type:this.product_type,
            device:'pc'
          }).then(res => {
            console.log(res)
            if (res.return_code == 0) {

              this.data_product = res.data
            }else{
              PopAlert(res.return_message)
            }
        });

      },
    }

  }

</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
.case
  .banner
    img
      width 100%
  .box-pro
    height 1460px
    position relative
    background-color #fff
    .pro-list
      li
        float left
        width 385px
        height 385px
        margin-right 10px
        margin-bottom 40px
        position relative
        cursor pointer
        &.no-margin
          margin-right 0
        img
          width 385px
          height 385px
        .pro-title
          position absolute
          bottom 0
          font-size 28px
          line-height 50px
          color #fff
          padding-left 15px
        .box-cover
          width 345px
          height 385px
          padding 0 20px
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          backface-visibility: hidden;
          background: rgba(0, 0, 0, 0.6);   /*后面这个0.6是指的背景的透明度*/
          opacity: 0;
          transition: all 0.35s ease-in-out;
          color #fff
          .cover-title
            display block
            color #ffffff
            font-size 28px
            padding 25px 0
            border-bottom 1px solid #ffffff
          .cover-cont
            padding 20px 0
            display block
            font-size 18px
            line-height 26px
            color #ffffff
        &:hover
          .box-cover
            opacity 1
          .pro-title
            display none
    .des
      height 480px
      background-color #fff
      width 1200px
      margin-left -600px
      position absolute
      left 50%
      top -80px
      box-shadow 0 0 20px 3px #dbdbdb
      .title-des-box
        text-align center
        padding 80px 0 40px 0
        .title
          font-size 54px
          line-height 60px
          margin-bottom 15px
        .des-cont
          font-size 22px
          line-height 30px
      ul
        display block
        padding-top 40px
        padding-left 110px
        li
          font-size 28px
          line-height 62px
          background-color #dbdbdb
          border-radius 30px
          padding 0 26px
          display inline-block
          text-align center
          cursor pointer
          color #252525
          margin-right 40px
          &.active
            background-color #976e01
            color #fff
  .box-center
    img
      width 100%
  .box-list
    background-color #ffffff
    padding 150px 0
    overflow hidden
    ul
      li
        width 283px
        float left
        margin-right 10px
        margin-bottom 70px
        cursor pointer
        text-align left
        .title
          margin-top 20px
          font-size 22px
          line-height 28px
          color #282828
        .desc
          font-size 18px
          line-height 24px
          color #505050


</style>





